import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';

import { Email } from '../models/email.model';

@Injectable({ providedIn: 'root' })

export class EmailsService {

  constructor(private http: HttpClient) {}


  addEmail(name: string, emailAdress: string, message: string) {
    const email: Email = { id: null, name, emailAdress, message };
    this.http
      .post<{ message: string, postId: string }>('http://oedb-env.bzmcwmpmmx.us-east-2.elasticbeanstalk.com/api/emails', email)
      .subscribe(responseData => {
        const id = responseData.postId;
        if (id === undefined || id === null ) {
          alert('failed');
        }
        alert('success');
      });
  }

}
