
import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgForm } from '@angular/forms';

import { EmailsService } from '../shared/services/email.service';


@Component({
  selector: 'app-email-me',
  templateUrl: './email-me.component.html',
  styleUrls: ['./email-me.component.css']
})
export class EmailMeComponent {
  enteredTitle = '';
  enteredContent = '';

  constructor(public emailsService: EmailsService) {}

  onAddEmail(form: NgForm) {
    if (form.invalid) {
      return;
    }
    this.emailsService.addEmail(form.value.name, form.value.emailAdress, form.value.message);
    form.resetForm();
  }
}
